import apis from '../utils/apis';
import { FETCH_PROPERTYLIST }  from './types';

export  const fetchProperties = () => dispatch => {
  apis.property.fetchProperties().then(data=>{
      dispatch({
          type:FETCH_PROPERTYLIST,
          payload:data.propertylist
      });
  });
};

